.chat {
	height: calc(100% - 72px);
}

.chat > * {
	background-color: #51B587;
	color: #fff;
	font-size: 16px;
	text-align: center;
	width: 100%;
}

#chatText {
	border-radius: 0;
  	font-size: 20px;
  	height: 60px;
}

#chatWindow {
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	padding-top: 12px;
	overflow-y: scroll;
	-ms-flex-direction: column-reverse;
	    flex-direction: column-reverse;
}

.stageHandler {
	border: none;
	border-radius: 0;
	font-size: x-large;
	padding-top: 30px;
	padding-bottom: 22px;
	cursor: pointer;
}

.mainWindow {
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	background: -webkit-gradient(linear, left top, left bottom, from(#F5FFFD), color-stop(#ECFFF3), color-stop(#E4FFF8), to(#D7F7F3));
	background: -webkit-linear-gradient(#F5FFFD, #ECFFF3, #E4FFF8, #D7F7F3);
	background: -o-linear-gradient(#F5FFFD, #ECFFF3, #E4FFF8, #D7F7F3);
	background: linear-gradient(#F5FFFD, #ECFFF3, #E4FFF8, #D7F7F3);
	display: -ms-flexbox;
	display: flex;
	color: #51B587;
  	height: 100%;
}

.navbar {
	background-color: #264D4D;
	font-weight: bold;
	padding-top: 21px;
    padding-bottom: 16px;
    font-size: 30px;
}

.prompt {
	display: inline-block;
}

.hidden {
	display: none;
}

.invisible {
	visibility: hidden;
}

textarea {
	font-family: inherit;
	padding: 6px 12px;
}

.message {
  -ms-flex-item-align: end;
      align-self: flex-end;
  background-color: #96D08E;
  border-radius: 12px 12px 0 12px;
  color: #fff;
  font-size: 16px;
  margin: 0 8px 12px 8px;
  max-width: 70%;
  padding: 8px 16px;
  text-align: left;
  word-wrap: break-word;
}

.partnerMessage {
	-ms-flex-item-align: start;
	    align-self: flex-start;
	background-color: #fff;
	border-radius: 12px 12px 12px 0;
	color: black;
	font-size: 16px;
	margin: 0 8px 12px 8px;
	max-width: 70%;
	padding: 8px 16px;
	text-align: left;
	word-wrap: break-word;
}


